.Toolbar{
    width: 100%;
    height: 52px;
    background-color: #CF8f2e;
    z-index: 90;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
    box-sizing: border-box;
    position: fixed;
    top: 0;
    left: 0;
}

.Toolbar nav{
    height: 100%;
}

@media (max-width: 499px){
    .DesktopOnly{
        display: none;
    }
}